import Icon from "../Icon/Icon";
import { inject, observer } from "mobx-react";
import useLang from "../../hooks/useLang";
import styled from 'styled-components'

const Wrapper = styled.div`
  font-size :14px;
  .contact {
    display : flex;
    justify-content : between-space;
    align-items : center;
    margin-top :16px;
    img {
      margin-right :16px;
    }
  }
`

function Footer({intl}){
  const lang = useLang(intl.dict,'footer');
  return (
    <Wrapper className='footer'>
      <div> 2021 {lang['deri-protocal']}</div>
      <div className='contact'>
        <Icon token='email' width='16' height='16'/>
        <Icon token='twitter' width='16' height='16'/>
        <Icon token='github' width='16' height='16'/>
        <Icon token='telegram' width='16' height='16'/>
      </div>
    </Wrapper>
  )
}
export default inject('intl')(observer(Footer))