import styled from "styled-components"
const Wrapper = styled.div`
  padding : 0 8px;
  height : ${props => props.height}px;
  color : ${props => props.color};
  border-color : ${props => props.borderColor};
  background-color : ${props => props.bgColor};
  border-radius : ${props => props.borderRadius};
  font-size : ${props => props.fontSize}px;
  text-align : center;
  .main {
    display : flex;
    align-items : center;
    justify-content : center;
  }
  img {
    margin-right :8px;
  }
`
export default function Label({text,height ,borderColor ,color,bgColor,borderRadius = '50%',fontSize,icon,className}) {

  return (
    <Wrapper className={className} height={height} borderColor={borderColor} color={color} bgColor={bgColor} borderRadius={borderRadius} fontSize={fontSize}>
      <span className='main'>
        {icon && <img src={icon} alt='' />}
        {text}
      </span>
    </Wrapper>
  )
}