import React, { useState, useEffect } from 'react'
import styled from 'styled-components';
import bscLogoIcon from '../../assets/img/bsc-logo.svg'
import arrowDownIcon from '../../assets/img/arrow-down.svg'
import Tooltip from '../Tooltip/Tooltip';
import { getNetworkList, getDefaultNw } from '../../utils/utils';
import { DeriEnv } from '../../lib/web3js';
import { inject, observer } from 'mobx-react';
const Wrapper = styled.div`
  display : flex;
  align-items : center;
  color : #93A1B8;
  width : 108px;
  margin-left : 16px;
  cursor : pointer;
  .name {
    margin-left : 4px
  }
  #network-select {
    .item {
      padding : 16px;
    }
    .item:hover,.item.selected {
      color : #E0ECFF;
      cursor : pointer;
    }
  }
`  
function NetworkSelector({wallet}){
  const [networkList, setNetworkList] = useState([])

  const calculatePosition = (position,
    currentEvent,
    currentTarget,
    refNode,
    place,
    desiredPlace,
    effect,
    offset) => {
      const rect = currentTarget.getBoundingClientRect();
      const top = rect.top + rect.height;
      const left = (rect.left - refNode.offsetWidth + rect.width)
      return {top:top + offset.top,left : rect.left}
    }

  useEffect(() => {
    const networkList = getNetworkList(DeriEnv.get());
    const defaultNw = getDefaultNw(DeriEnv.get());
    wallet.setDefaultNw(defaultNw)
    setNetworkList(networkList)
  }, [wallet])


  return (
    <Wrapper className='network-select' data-tip data-for='network-select'  data-event='click'>
      <img src={bscLogoIcon} alt='bsc'/>
      <div className='name'>BSC</div>
      <img src={arrowDownIcon} alt='select' />
      <Tooltip id='network-select' width = {180} offset={{top : 14}} overridePosition={calculatePosition}>
        {networkList.map((network,index) => (
          <div key={index} className={`item ${wallet.detail.code && network.code === wallet.detail.code ? 'selected' : network.isDefault ? 'selected' : ''}`} onClick={() => wallet.switchNetwork(network)}>
            <span className={`logo ${network.symbol}`}></span><span>{network.name}</span>
          </div>)
        )}
      </Tooltip>
    </Wrapper>
  )
}
export default inject('wallet')(observer(NetworkSelector))